// Module:包装码下的SKU码
<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="handleCancel"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <el-form ref="filterItem" class="top-filter clearfix" :model="query" inline @submit.native.prevent>
          <el-form-item prop="barCode">
            <el-input v-model.trim="query.barCode" placeholder="条码号" class="filter-item" style="width: 178px;" clearable @keyup.enter.native="searchInit" />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="init"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
          <el-button
            v-if="isShowPrintBtn"
            class="filter-item"
            type="primary"
            :disabled="!tableSelection.length"
            @click="handlePrint(tableSelection.map(c => c.id))"
          >批量打印</el-button>
        </el-form>
        <el-row>
          <table-com
            :ref="refName"
            :loading="loading"
            :index="true"
            :index-fixed="false"
            :selection="true"
            :selection-fixed="true"
            :table-column-sort="false"
            :table-height-type="620"
            :table-data="tableData"
            :show-operate="isShowPrintBtn"
            :operate-fixed="false"
            :operation-width="120"
            :column-list="columnList"
            :table-selection="tableSelection"
            :row-key="rowKey"
            @selectionChange="selectionChange"
          >
            <template slot-scope="scope">
              <el-button type="primary" @click="handlePrint([scope.row.id])">打印条码</el-button>
            </template>
          </table-com>
        </el-row>
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm">确 定</el-button>
      <el-button @click="handleCancel">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
export default {
  name: 'PackageToSkuDialog',
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    rowKey: {
      type: [String, Number],
      default: 'id'
    },
    isShowPrintBtn: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      loading: false,
      refName: 'tableList',
      query: {
        barCode: ''
      },
      tableData: [],
      columnList: [],
      tableSelection: []
    }
  },
  watch: {
    extraInfo: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  methods: {
    init() {
      if (!this.query.barCode) {
        this.tableData = this.extraInfo.tableData
      } else {
        this.tableData = this.extraInfo.tableData.filter(c => c.barCode === this.query.barCode)
      }
      const rowKey = this.rowKey
      const ids = this.tableSelection.map(i => i[rowKey])
      this.tableData.forEach(row => {
        function check(row) {
          const id = row[rowKey]
          return ids.some(i => i === id)
        }
        if (check(row)) {
          this.$nextTick(() => {
            this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
          })
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.tableData = this.extraInfo.tableData
    },
    initSelect() {
      this.columnList = this.extraInfo.columnList
      this.tableData = this.extraInfo.tableData
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    handlePrint(selection) {
      this.$emit('print', selection)
    },
    handleCancel(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
      this.$nextTick(() => {
        this.resetForm('filterItem')
        this.$refs[this.refName].$refs.multipleTable.clearSelection()
        this.tableSelection = []
        this.tableData = []
      })
    },
    handleConfirm() {
      this.handleCancel()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
.main-box {
  padding: 10px;
  box-sizing: border-box;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
